import { PageContent } from '../../components';
import React, { useState } from 'react';
import { FilterMatchMode, FilterOperator } from 'primereact/api';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Dropdown } from 'primereact/dropdown';

import {
    data,
    idBodyTemplate,
    nameBodyTemplate,
    actionsBodyTemplate,
    getCustomers,
    countryBodyTemplate,
    dateBodyTemplate,
    lastDateBodyTemplate
} from './data';
import useCustomEffect from '../../hooks/useCustomEffect';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';


const CustomerService = {
    getCustomersMedium: function () {
        return new Promise((resolve, reject) => {
            resolve(data);
        });
    }
};


export default function CitiesList() {

    const [selectedEntries, setSelectedEntries] = useState({ name: 4, code: 4 });

    const entries = [];

    data.map((item, index) => entries.push({ name: index + 1, code: index + 1 }));

    const [customers, setCustomers] = useState(null);
    const [filters, setFilters] = useState(null);
    const [loading, setLoading] = useState(false);
    const [globalFilterValue, setGlobalFilterValue] = useState('');

    const { useReplacePagnitToText, useFetchData } = useCustomEffect();

    useReplacePagnitToText();
    const initFilters = () => {
        setFilters({
            global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            name: { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }] },
        });
        setGlobalFilterValue('');
    };

    useFetchData(CustomerService, setCustomers, getCustomers, setLoading, initFilters);

    return (

        <PageContent
            url={'add-city'}
            title={'City Form'}
            showActions={true}
        >

            <div className='flex items-center justify-between px-10 mb-5'>

                <div className='flex items-center'>

                    <h3 className='text-[#252525] font-medium me-3'>Show</h3>

                    <Dropdown value={selectedEntries} onChange={(e) => setSelectedEntries(e.value)} options={entries} optionLabel="name"
                        placeholder="" className="w-full md:w-14rem" />

                    <h3 className='text-[#252525] font-medium ms-3'>entries</h3>

                </div>

                <div className='relative min-w-[400px]'>

                    <input type='text' placeholder='Search (Ctrl+/)' className='px-10 py-3 rounded-full border w-full' />

                    <FontAwesomeIcon icon={faSearch} className='text-[#252525] text-[13px] absolute left-4 top-[50%] translate-y-[-50%]' />

                </div>


            </div>


            <DataTable value={customers} paginator showGridlines rows={selectedEntries.name} loading={loading} dataKey="id"

                filters={filters} globalFilterFields={['name', 'country.name', 'representative.name', 'balance', 'status']}
                emptyMessage="No Products Found." className='px-8'>

                <Column field="id" header="Id" className='lg:w-[30px] !px-[15px]' body={idBodyTemplate} />

                <Column field='name' header="Name" className='lg:w-[30px] !px-[0px]' body={nameBodyTemplate} />

                <Column field='country' header="Country" className='lg:w-[30px] !px-[0px]' body={countryBodyTemplate} />

                <Column field='date' header="Date" className='lg:w-[200px] !px-[15px]' body={dateBodyTemplate} />

                <Column field="lastDate" header="Last Date" className='lg:w-[200px] !px-[15px]' body={lastDateBodyTemplate} />

                <Column field="STATUS" header="Action" className='lg:w-[100px] !px-[15px]' body={actionsBodyTemplate} />


            </DataTable>


        </PageContent>

    )
}
